export class UrlHelper{
    public static IsDirectUrl(url: string) {
        return url.startsWith("http:") || url.startsWith("https:") || url.startsWith("blob:");
    }
    public static GetDomain(urlString: string){
        let url: {hostname: string, port: string, search: string, pathname: string, protocol: string};
        try{
            url = new URL(urlString);
        }catch(ex){
            const a = document.createElement("a");
            a.href = urlString;
            url = a;
        }
        return url.protocol + "//" + url.hostname + (url.port ? ":" + url.port : "");
    }
    public static ParseQuery(queryString: string | undefined) {
        var query:Record<string, Array<string> | undefined> = {};
        if (!queryString) { return query; }

        var pairs = (queryString[0] === '?' ? queryString.substr(1) : queryString).split('&');
        for (var i = 0; i < pairs.length; i++) {
            var pair = pairs[i].split('=');
            const curKey = decodeURIComponent(pair[0]);
            let curItem = query[curKey];
            if (!curItem){
                curItem = [];
                query[curKey] = curItem;
            }
            curItem.push(decodeURIComponent(pair[1] || ''));
        }
        return query;
    }
}