import { QJsonParserV2, removeCR } from "./QJsonParserV2";
import { IQJsonParser } from "./IQJsonParser";
import { QJsonContentParser } from "./QJsonContentParser";


export class QJsonExtractHelper {
    
    public static qJsonExtractor(qjsonData: string | { qjson: string }) {
        const jsonString = QJsonExtractHelper.qJsonMediumDecider(qjsonData);
        
        if (jsonString.startsWith("qjson-")) {
            let version = removeCR(jsonString.substring("qjson-".length, jsonString.indexOf("\n")));
            return parsers[version].Parse(jsonString);
        }

        return QJsonExtractHelper.ParseQJsonContent(jsonString);
    }


    private static qJsonMediumDecider(qjsonData: string | { qjson: string }): string {
        if (typeof qjsonData == "string") { //rally | symphony
            return qjsonData.startsWith('{"qjson":"') ? JSON.parse(qjsonData).qjson : qjsonData; // symphony : other
        } else {
            const qjsonString = JSON.stringify(qjsonData);
            return qjsonData.qjson || qjsonString; //symphony & websdk.axios
        }
    }

    private static ParseQJsonContent(jsonString: string) {
        return QJsonContentParser.ParseQJsonContent(jsonString);
    }
}

const parsers: Record<string, IQJsonParser> = { v2: new QJsonParserV2() };