// import { Router } from "./src/Router";
import yaml from "js-yaml";
import { LazyLoader } from "../../common/shrimp/helpers/lazyLoader";
import { QJsonExtractHelper } from "../../common/shrimp/helpers/qjsonParser/QJsonExtractHelper";
import { UrlHelper } from "../../common/shrimp/helpers/urlHelper";
import { ILRID } from "../../common/shrimp/interfaces/ComponentInterfaces/ILocalResource";
import { ComponentOptions } from "../../common/shrimp/interfaces/ComponentInterfaces/IPropExtension";
import { IQJSon } from "../../common/shrimp/interfaces/ComponentInterfaces/IQJson";
import { IContainerServices } from "../../common/shrimp/interfaces/quick/IContainerServices";
import { IWebSDKCtor, IWebSDKSettings } from "../../common/shrimp/interfaces/quick/IWebSDK";
import { IRenderer } from "../../common/shrimp/interfaces/RenderingInterfaces/IRenderer";
import { IRendererConfig } from "../../common/shrimp/interfaces/RenderingInterfaces/IRendererConfig";
import { createRenderer,init } from "@stechquick/websdk";

const quiRootParent = document.createElement("div");
const quiRoot = document.createElement("div");
quiRootParent.appendChild(quiRoot);


export async function CreateQui(webSDKCtor: IWebSDKCtor) {
    const settings = await LoadSettings();
    const globalLR = await LoadGlobalLR();
    let componentOptions: ComponentOptions | undefined = await LoadComponentList(settings?.componentListURL);
    if (!settings) { return; }
    let renderer: IRenderer;

    let getURL: IContainerServices["getURL"] | undefined;

    //undocumented noRelativeQjsonUrl feature.
    if ((settings as any).noRelativeQjsonUrl) {
        getURL = (url, mode) => {
            if (mode != "ui" || UrlHelper.IsDirectUrl(url)) { return undefined; }

            return url.startsWith("/") ? url : "/" + url;
        };
    }
    //undocumented noRelativeQjsonUrl feature.
    let sdk = new webSDKCtor(
        settings,
        { extensions: {}, getURL });


    const config: IRendererConfig = {
        GlobalLRDict: globalLR,
        SiteSettings: { compSettings: componentOptions },
        LRType: settings.rootLangCode
    };


    renderer = sdk.CreateRenderer({ mountPoint: quiRoot, config });

    if (settings.pipelineqjson) {
        const qjson = await LoadQjson(settings.pipelineqjson);
        if (qjson) {
            try {
                await renderer.settingsQJsons.SetPipelineQjson(qjson);
            } catch (err) {
            }
        }
    }

    if (settings.alertqjson) {
        const qjson = await LoadQjson(settings.alertqjson);
        if (qjson) {
            renderer.settingsQJsons.SetAlertQjson(qjson);
        }
    }

    if (settings.loadingqjson) {
        const qjson = await LoadQjson(settings.loadingqjson);
        if (qjson) {
            renderer.settingsQJsons.SetLoadingQjson(qjson);
        }
    }

    if (settings.domainModuleList) {
        try {
            await sdk.LoadModuleCompLists(settings.domainModuleList);
        } catch (err) {
        }
    }

    let pjsonPath = settings.rootqjson;

    let search = window.location.search;
    let query: Record<string, Array<string> | undefined> = {};
    if (search) {
        search = search[0] === '?' ? search.substr(1) : search;
        search = search.replace(/\?/, "&");
        query = UrlHelper.ParseQuery(search);
        pjsonPath = query.q ? query.q.splice(0, 1).join("") : pjsonPath;
        if (query.q?.length == 0) { delete query.q; }
    }
    if (!pjsonPath) {
        return;
    }
    if (query.qjsonpath) {
        //this is symphony -> "q=/isleasing-sym/qjsonfunc?qjsonpath=homeTablet"
        pjsonPath += "?qjsonpath=" + query.qjsonpath.splice(0, 1);
        if (query.qjsonpath.length == 0) { delete query.qjsonpath; }
    }
    const leftQueryString = Object.entries(query).map(([key, value]) => {
        return value?.map(eachValue => `${encodeURIComponent(key)}=${encodeURIComponent(eachValue!)}`)
    }).map(item => item?.join("&")).join('&');
    const pageName = leftQueryString ? pjsonPath + "?" + leftQueryString : pjsonPath;

    pjsonPath += ".qjson";
    renderer.Render({ pjsonPath, pageName });
    //renderer2.Render({ pjsonPath, pageName });
    const ux = sdk.getUXManager();
    ux.setTheme("default");
    document.body.appendChild(quiRootParent);
}

async function LoadSettings() {
    try {
        const settingsBody = (await LazyLoader.FetchModuleBody("settings/settings.yaml")).body;
        const settingsYaml = yaml.load(settingsBody) as object;
        if (typeof (settingsYaml) != "object") {
            throw new Error("settings.yaml has error");
        }
        const settings: IWebSDKSettings = settingsYaml;
        return settings;
    } catch (ex) {
    }
}

async function LoadGlobalLR() {
    try {
        const qJson = await LoadQjson('settings/globalLocalization.qjson');
        const globalLR: ILRID | undefined = qJson.clrids;
        return globalLR;
    } catch (ex) {
    }
}

export async function LoadQjson(qjsonPath: string) {
    const qJsonString = (await LazyLoader.FetchModuleBody(qjsonPath)).body;
    const qJsonExtracted = <IQJSon>QJsonExtractHelper.qJsonExtractor(qJsonString)
    if (!qJsonExtracted) {
        throw new Error("qjson has error");
    }
    return qJsonExtracted;
}

async function LoadComponentList(url?: string) {
    return LazyLoader.FetchModuleBody(url ? `${url}/componentList.js` : `static/js/componentList.js`).then(function (moduleBody) {
        const settings = LazyLoader.LoadModule<ComponentOptions>(moduleBody.body);
        if (!settings) {
            return undefined;
        }
        return settings;
    }).catch(err => {
        return undefined;
    })
}

declare var window: Window & { websdk: { createRenderer: typeof createRenderer, init: typeof init } };

 var PlateauUIOptions = {
    corsEnabled: true,
    filePathsForSettings: {
        settingsFilePath: "settings/settings-proxy.js",
        componentListFilePath: "static/js/componentList.js",
        containerServicesFilePath: "static/js/containerServices.js"
    }
}

window.websdk.init(PlateauUIOptions).then(()=>{
    var renderer = window.websdk.createRenderer({ mountPoint: quiRoot });
    renderer.open();
    document.body.appendChild(quiRootParent);
});
