//IE11 fetch pollyfill https://github.com/github/fetch -> npm: whatwg-fetch
//IE11 promise pollyfill https://www.npmjs.com/package/promise-polyfill -> npm: promise-polyfill
interface ModuleObject<exportingInterface> {
    exports?: exportingInterface
}

export class LazyLoader {

    static LoadModule<exportingInterface>(moduleBody: string) {
        const moduleObject: ModuleObject<exportingInterface> = {};
        const module = new Function("module", "exports", moduleBody);
        const temp_export = {};
        module(moduleObject, temp_export);
        const exported = moduleObject.exports;
        return exported;
    }

    static FetchModuleBody(url: string) {
        return new Promise<{ body: string, status: number }>((res, rej) => fetch(url).then(response => response.text().then(body => res({ body, status: response.status })).catch(rej)).catch(rej));
    }
}