import { IQJSon } from "../../interfaces/ComponentInterfaces/IQJson";
import { IQJsonParser } from "./IQJsonParser";
import { QJsonContentParser } from "./QJsonContentParser";

type QJsonFileHeaderTypes = "source" | "compiled" | "qjson";

interface QJsonFileHeaders extends Record<string, string | undefined> {
    type: QJsonFileHeaderTypes;
    name: string;
}

class QJsonParserV2 implements IQJsonParser {
    private static boundary = "-------------------" + "QJSONFileBoundary" + "-----------------------------";
    public static headerPrefix = "qjson-";

    public static QJsonBodyToString(qjson: IQJSon, unformatQjson?: boolean) {
        return "let pjson = " +
           ( unformatQjson == true ? JSON.stringify(qjson) : JSON.stringify(qjson, null, '\t') ) +
            ";\r\n" +
            "export default pjson";
    }


    public Serialize(qjson: IQJSon) {
        const bodundary = `${QJsonParserV2.boundary}\r\n`;
        const header = `${QJsonParserV2.headerPrefix}v2\r\nboundary: ${bodundary}\r\n${bodundary}`;
        const serializeFile = (fileName: string, type: QJsonFileHeaderTypes, fileBody: string) => `name: ${fileName}\r\ntype: ${type}\r\n\r\n${fileBody}`;
        const files = qjson.files;
        const cFiles = qjson.cFiles;
        let qjsonFile;
        try { qjson.files = undefined; qjson.cFiles = undefined; qjsonFile = serializeFile("qjson", "qjson", QJsonParserV2.QJsonBodyToString(qjson)); }
        finally { qjson.files = files; qjson.cFiles = cFiles } // atomic
        const fileItems = files ? bodundary + Object.keys(files).map(fileName => serializeFile(fileName, "source", files[fileName])).join(bodundary) : "";
        const cFileItems = cFiles ? bodundary + Object.keys(cFiles).map(fileName => serializeFile(fileName, "compiled", cFiles[fileName])).join(bodundary) : "";
        return header + qjsonFile + fileItems + cFileItems;
    }

    public Parse(jsonString: string) {
        let curLine = this.nextLine(jsonString, 0);

        let fileHeaders = this.extractHeaders(jsonString, curLine.lastIndex);
        const boundary = fileHeaders.headers.boundary!;
        let lastIndex = fileHeaders.lastIndex;

        const files: Record<string, { headers: QJsonFileHeaders, content: string }> = {};
        const cFiles: typeof files = {};
        do {
            const section = this.getSection(jsonString, boundary, lastIndex);
            const targetFiles = section.headers.type == "compiled" ? cFiles : files;
            targetFiles[section.headers.name!] = { headers: section.headers, content: section.section };
            lastIndex = section.lastIndex;
        } while (lastIndex != -1);

        const qjson: IQJSon = QJsonContentParser.ParseQJsonContent(files["qjson"].content);
        delete files.qjson;

        const qjsonFiles: typeof qjson.files = qjson.files = {};
        const qjsonCFiles: typeof qjson.cFiles = qjson.cFiles = {};
        Object.keys(files).forEach(fileName => qjsonFiles[fileName] = files[fileName].content);
        Object.keys(cFiles).forEach(fileName => qjsonCFiles[fileName] = cFiles[fileName].content);
        return qjson;
    }

    private getSection(str: string, boundary: string, lastIndex: number): { headers: QJsonFileHeaders; section: string; lastIndex: number; } {
        let headers = this.extractHeaders(str, lastIndex);
        const section = this.getTillBoundary(str, boundary, headers.lastIndex);
        return { headers: headers.headers, section: section.section, lastIndex: section.lastIndex };
    }

    private getTillBoundary(str: string, boundary: string, lastIndex: number): { section: string, lastIndex: number } {
        const nextBoundaryIndex = str.indexOf(boundary, lastIndex);
        if (nextBoundaryIndex == -1) {
            return { section: str.substring(lastIndex), lastIndex: -1 };
        }
        return { section: str.substring(lastIndex, nextBoundaryIndex), lastIndex: nextBoundaryIndex + boundary.length + 1 };
    }
    private extractHeaders(str: string, lastIndex: number) {
        const headersBucket: Record<string, string | undefined> = {};
        let curLine = { line: "", lastIndex };
        do {
            curLine = this.nextLine(str, curLine.lastIndex);
            if (!curLine.line) { break; } // headers section end

            const lineData = this.parseLine(curLine.line);
            if (!lineData) { continue; } // line is not parsable skip

            headersBucket[lineData.key] = lineData.value;
        } while (curLine.lastIndex != -1);

        return { headers: headersBucket as QJsonFileHeaders, lastIndex: curLine.lastIndex };
    }



    private parseLine(line: string) {
        const commaIndex = line.indexOf(":");
        if (commaIndex == -1) { return undefined; }

        return { key: line.substring(0, commaIndex).trim(), value: line.substring(commaIndex + 1).trim() };
    }

    private nextLine(str: string, lastIndex: number): { line: string, lastIndex: number } {
        const lineEnd = str.indexOf("\n", lastIndex);
        let line = removeCR(str.substring(lastIndex, lineEnd == -1 ? undefined : lineEnd));
        return { line, lastIndex: lineEnd + 1 || -1 };
    }
}

function removeCR(str: string) {
    return str.endsWith("\r") ? str.substring(0, str.length - 1) : str;
}

export { QJsonParserV2, removeCR };